import { getNumber } from 'components/NumberText';
import { createChart, CrosshairMode, IChartApi, ISeriesApi, OhlcData, UTCTimestamp } from "lightweight-charts";
import moment from 'moment';
import * as React from "react";
import { useCallback, useEffect, useRef, useState } from "react";
import { useKLists } from 'state/poolAnalysis/hooks';
import { useFetchKListCallback } from 'state/poolAnalysis/useFetchCallback';
import { useDarkModeManager } from 'state/user/hooks';
import { useWindowSize } from '../../hooks/useWindowSize';
import { KSpan, ToolTip } from './Components';

function splitData(rawData: (number | string)[][], tspan: number) {
  const candleSeries: Array<OhlcData> = [];
  const volumeSeries = [];
  for (let i = 0; i < rawData.length; i++) {
    try {
      const d: any = [];
      let j = 0;
      for (const item of rawData[i]) {
        if (j === 0) {
          if (candleSeries.length > 0) {
            const next = Number(candleSeries[candleSeries.length - 1].time) + tspan as UTCTimestamp;
            if (next > item) {
              break
            }
          }
          d.push(item)
        }
        else d.push(getNumber(item, '0,0.0000000', 6));
        j++;
      }
      if (d[0]) {
        const t = parseInt(d[0]) as UTCTimestamp
        candleSeries.push({
          time: t,
          open: d[1],
          high: d[2],
          low: d[3],
          close: d[4],
        });
        debugger
        volumeSeries.push({
          time: t,
          value: d[5],
          color: d[1] < d[4] ? "rgba(0, 150, 136, 0.8)" : 'rgba(255,82,82, 0.8)',
        })
      }
    } catch (error) {
      debugger
    }

  }

  return [candleSeries, volumeSeries];
}
const k = [
  { text: '1m', value: 1 },
  { text: '3m', value: 3 },
  { text: '5m', value: 5 },
  { text: '15m', value: 15 },
  { text: '30m', value: 30 },
  { text: '1h', value: 60 },
  { text: '4h', value: 240 },
  { text: '12h', value: 720 },
  // { text: '1d', value: 1440 },
  // { text: '1w', value: 10080 }
]
const Lightweight = (props: { name: string, pairAddresses?: string }) => {
  const refLegend = useRef<HTMLDivElement>(null);
  const { name, pairAddresses } = props;
  const d = useKLists();
  const [darkMode,] = useDarkModeManager();
  const [t, setT] = useState(60);
  const [candleData, volumeData] = splitData(d, t * 60);
  // const chartRef = useRef<HTMLDivElement>(null);
  const { width = 0, height } = useWindowSize();
  // const measuredRef = useCallback(node => {
  //   if (node !== null) {
  //     setHeight(node.getBoundingClientRect().height);
  //   }
  // }, []);
  const chart = useRef<IChartApi>();
  const candlestick = useRef<ISeriesApi<"Candlestick">>();
  const volumeSeries = useRef<ISeriesApi<"Histogram">>();
  const bg = darkMode ? '#000000' : '#efefef';
  const loadChart = useCallback((dom: HTMLDivElement) => {
    if (!chart.current) {
      chart.current = createChart(dom, {
        width: width * 0.9,
        height: 300,
        layout: {
          backgroundColor: bg,
          textColor: 'rgba(201, 199, 199, 0.9)',
        },
        grid: {
          vertLines: {
            color: 'rgba(197, 203, 206, 0.5)',
          },
          horzLines: {
            color: 'rgba(197, 203, 206, 0.5)',
          },
        },
        crosshair: {
          mode: CrosshairMode.Normal,
        },
        rightPriceScale: {
          borderColor: 'rgba(197, 203, 206, 0.8)',
        },
        timeScale: {
          borderColor: 'rgba(197, 203, 206, 0.8)',
        }
      });

      chart.current.applyOptions({
        timeScale: {
          rightOffset: 11,
          timeVisible: true,
          secondsVisible: true,
          tickMarkFormatter: (time: any) => moment.unix(time).format('YYYY/MM/DD HH:mm')
        },

        // priceScale: { autoScale: true },
      });

      candlestick.current = chart.current.addCandlestickSeries({
        upColor: 'rgba(255, 144, 0, 1)',
        downColor: bg,
        borderDownColor: 'rgba(255, 144, 0, 1)',
        borderUpColor: 'rgba(255, 144, 0, 1)',
        wickDownColor: 'rgba(255, 144, 0, 1)',
        wickUpColor: 'rgba(255, 144, 0, 1)',
      });

      volumeSeries.current = chart.current.addHistogramSeries({
        color: "#26a69a",
        priceFormat: {
          type: "volume",
        },
        priceScaleId: "",
        scaleMargins: {
          top: 0.8,
          bottom: 0,
        },
      });


      // update tooltip
      chart.current.subscribeCrosshairMove(function (param) {
        const t = param.time as UTCTimestamp;
        if (param.time) {
          param.seriesPrices.forEach(function (value: any, key) {
            if (value.open && refLegend.current) {
              refLegend.current.innerHTML = `<div>${moment.unix(t).format('YYYY/MM/DD HH:mm')}</div>
              <div>open:${value.open}</div>
              <div>high:${value.high}</div>
              <div>low:${value.low}</div>
              <div>close:${value.close}</div>`;
            }
          });
        } else {
          if (refLegend.current) refLegend.current.innerHTML = '';
        }
      });
    }
  }, [bg, width]);
  useEffect(() => {
    if (candlestick.current && volumeSeries.current) {
      // debugger
      console.log('volumeData', volumeData)
      volumeSeries.current.setData(volumeData);
      candlestick.current.setData(candleData);
    }
  }, [candleData, volumeData, width]);

  const getKLine = useFetchKListCallback();
  useEffect(() => {
    // debugger
    if (pairAddresses)
      getKLine(`/api/Fst/kline?interval=${t}&pair=${pairAddresses}`);
    // getKLine(`https://api.fstswap.vip/api/Fst/kline?interval=${t}&pair=0xb4Ec801aED8C92F2E69589518AAa127afb37d8C9&limit=1000`);
  }, [getKLine, pairAddresses, t])
  return (
    <div style={{ position: 'relative', background: bg }}>
      <KSpan>
        {k.map(p => <span key={p.value} onClick={() => setT(p.value)} className={p.value == t ? 'active' : ''}>{p.text}</span>)}
      </KSpan>
      <ToolTip ref={refLegend}>
      </ToolTip>
      <div ref={loadChart} style={{ width: '90%' }} />
    </div>
  );
};

export default Lightweight;